import { Component, OnInit } from '@angular/core';
import { StaffService } from '../../../../../auth/_services/staff.service';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
    selector: 'app-staff-list',
    templateUrl: "staff-list.component.html",
    styles: [],
    providers: [StaffService]
})
export class StaffListComponent implements OnInit {

    form: FormGroup;
    form1: FormGroup;
    private formSubmitAttempt: boolean;

    constructor(private _staffService: StaffService, private toastr: ToastrService, private router: Router, private formBuilder: FormBuilder) { }
    public staffData;
    public staffDataByid;
    public searchValue = "";
    public dropDownValue = '';
    public staff_id;
    p: number = 1;

    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.support_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }

        this.form = this.formBuilder.group({
            f_name: [null, Validators.required],
            l_name: [null, Validators.required],
            email: [null, Validators.required],
            contact_no: [null, Validators.required],
        });

        this.form1 = this.formBuilder.group({
            id: null,
            f_name: [null, Validators.required],
            l_name: [null, Validators.required],
            email: [null, Validators.required],
            contact_no: [null, Validators.required],
        });

        /*user permission*/

        // localStorage.setItem('section', 'customer');

        this.getAll();
        
    }

    getAll() {
        this._staffService.getAll().subscribe(
            data => {
                //console.log(data);
                this.staffData = data.success;
            }
        );
    }

   onSubmit() {
        let input: any;
        this.formSubmitAttempt = true;
        if (this.form.valid) {
            input = this.form.value;
            console.log(input);
            this._staffService.create(input).subscribe(
                data => {
                    this.toastr.success('Staff Added Successfully', 'Success Message');
                    this.reset();
                    this.getAll();
                },
                (error: any) => {
                    console.log(error);
                    console.log('error');
                }
            );
        }
    }

    reset() {
        this.form.reset();
    }

      public staff_delete(del_id) {

        console.log(del_id);

        let value = confirm('Are you sure to delete this staff request?');
        if (value) {

            this._staffService.del_staff(del_id).subscribe(
                data => { console.log(data);
                    this.getAll();
                    this.toastr.success('Staff deleted Successfully', 'Success Message');
                    //this.customerData = data.data;
                }
            );
        }

    }

    onEdit(staff_id) {
        this._staffService.getById(staff_id).subscribe(
            data => {
                this.staffDataByid = data.success;
                this.staff_id = staff_id;
            }
        );
    }

    onUpdate() {
        
        let input: any;
        this.formSubmitAttempt = true;
        
        if (this.form1.valid) {
            input = this.form1.value;
            /*console.log(input);
            console.log(this.staff_id);*/
            this._staffService.update(input,this.staff_id).subscribe(
                data => {
                    this.toastr.success('Staff updated Successfully', 'Success Message');
                    this.reset();
                    this.getAll();
                },
                (error: any) => {
                    console.log(error);
                    console.log('error');
                }
            );
        }
    }

}
